  function playlistCountDisplay(count) {
    if (!count) {
      return "";
    }

    var label = "videos";
    if (count === 1) {
      label = "video";
    }
    return count + " " + label;
  }

  function playlistDurationDisplay(seconds) {
    if (!seconds) {
      return "";
    }
    var hours = Math.floor(seconds / 3600);
    seconds = seconds - 3600 * hours;
    var minutes = Math.round(seconds / 60);

    var str = minutes + " mins";
    if (hours > 0) {
      var label = "hrs";
      if (hours === 1) {
        label = "hr";
      }
      str = hours + " " + label + " " + str;
    }
    return str;
  }

  function videoDurationDisplay(seconds) {
    if (!seconds) {
      return "";
    }
    var hours = Math.floor(seconds / 3600);
    seconds = seconds - 3600 * hours;
    var minutes = Math.floor(seconds / 60);
    seconds = seconds - 60 * minutes;

    var str = ":" + String(seconds).padStart(2, "0");

    if (hours > 0) {
      str = hours + ":" + String(minutes).padStart(2, "0");
    } else {
      str = minutes + str;
    }
    return str;
  }

  let validatingLicenseKey = false;
  $: licenseKeyAreaOverlayActive = validatingLicenseKey;
  let aboutModalActive = false;

  var localhost_regex = /localhost/;
  if (localhost_regex.test(window.location.href)) {
    devMode = true;
  }

  let now = dayjs(new Date());

  let license = initLicense();

  function initLicense() {
    return {
      key: null,
      active: false,
      expired: false,
      expiresAt: null,
      lastCheckedAt: null,
    };
  }

  function checkLicenseKey(e) {
    const { name, value } = e.target;

    if (licenseKeyErrorText) {
      licenseKeyErrorText = null;
    }

    if (value.length != 36) {
      return;
    }

    console.log("We have a 36-character license key entry: " + value);
    validateLicenseKey(value);
  }

  function validateLicenseKey(key) {
    validatingLicenseKey = true;
    licenseKeyErrorText = null; // reset any error message

    key = key.trim();
    var endpoint = "https://api.verkout.com/license_keys/" + key + "/use";

    var requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
    };

    let fetchJson = fetch(endpoint, requestOptions)
      .then((res) => {
        console.log("got a response....");
        console.log(res);
        return res.json();
      })
      .then((data) => {
        console.log("...and here's the structured info:");
        console.log(data);
        if (data.errors) {
          licenseKeyAreaOverlayActive = false;
          var error = data.errors[0];
          licenseKeyErrorText = error.detail;
          if (error.expiredAt) {
            licenseKeyErrorText =
              licenseKeyErrorText +
              " It expired on " +
              dayjs(error.expiredAt).format("dddd, MMMM D, YYYY h:mm A") +
              ".";
          } else if (error.revokedAt) {
            licenseKeyErrorText =
              licenseKeyErrorText +
              " It was revoked on " +
              dayjs(error.revokedAt).format("dddd, MMMM D, YYYY h:mm A") +
              ".";
          }
        } else if (data.license) {
          licenseKeyAreaOverlayActive = false;
          license.key = data.license.key;
          license.firstUsedAt = data.license.firstUsedAt;
          license.expiresAt = data.license.expiresAt;
          license.active = true;

          localStorage.setItem("license", JSON.stringify(license));
        }
      });
  }

  if (companionContent) {
    const storedLicense = localStorage.getItem("license");

    if (storedLicense) {
      license = JSON.parse(storedLicense);
      if (license.key) {
        validateLicenseKey(license.key);
      }
    }
  }

  function toggleLicenseKeyArea() {
    licenseKeyAreaActive = !licenseKeyAreaActive;
  }

  function openLicenseKeyArea() {
