<div
  class="fixed {!aboutModalActive
    ? 'hidden'
    : ''} z-50 inset-0 bg-black bg-opacity-70 overflow-y-auto h-full w-full"
  id="about-modal"
  on:click={() => toggleAboutModal()}
>
  <div
    class="relative top-24 mx-auto py-5 border border-black w-96 shadow-lg rounded-md bg-white"
    on:click|stopPropagation={() => noop()}
  >
    <div class="mt-3 text-center">
      <h3 class="text-lg font-medium text-black uppercase">About</h3>
      <div class="mt-2 pt-4 px-4 text-left">
        <p class="text-sm text-charcoal">
          This is <span class="text-black font-bold"
            >The Engineering Manager's Companion</span
          >, a video compendium for software development leaders.
        </p>
        <p class="text-sm text-charcoal pt-6">
          <span class="text-black font-bold">License Key</span><br />
          {license.key}
        </p>
        <p class="text-sm text-charcoal pt-6">
          <span class="text-black font-bold">First Used On</span><br />
          {dayjs(license.firstUsedAt).format("dddd, MMMM D, YYYY h:mm A")}
        </p>
        <p class="text-sm text-charcoal pt-6">
          <span class="text-black font-bold">Expires On</span><br />
          {dayjs(license.expiresAt).format("dddd, MMMM D, YYYY h:mm A")}
        </p>
      </div>
      <div class="items-center px-4 py-6 pt-8">
        <button
          class="px-4 py-2 bg-red100 text-white font-medium rounded-md w-14 shadow-sm uppercase"
          on:click={() => toggleAboutModal()}
        >
          OK
        </button>
        {#if devMode}
          <button
            class="px-4 py-2 bg-gold text-black font-medium rounded-md w-44 shadow-sm uppercase"
            on:click={() => clearLicense()}
          >
            Clear
          </button>
        {/if}
      </div>
    </div>
  </div>
