<script>
  import Icon from "./Icon.svelte";

  export let secondary = false;
  export let active;
  export let name;
  export let iconName;
  export let title;
  export let onActivate;
  export let classes;

  // var inactiveColor = "#000000";
  // var inactiveColorClass = "black";

  var inactiveColor = "#575757";
  var inactiveColorClass = "charcoal";

  var activeColor = "#D20000";
  var activeColorClass = "red100";

  $: color = active ? activeColor : inactiveColor;
  $: colorClass = active ? activeColorClass : inactiveColorClass;
  $: borderColorClass = active && !secondary ? activeColorClass : "transparent";
  $: boldClass = active ? "font-bold" : "";

  function onIconClick(section) {
    console.log("onIconClick with section " + section);
    if (onActivate) {
      onActivate(section);
    }
  }
</script>

<div
  class="flex flex-row justify-items-start             
            items-center p-2 pl-8 {secondary ? 'py-0' : ''} {boldClass}
            mb-1 text-{colorClass} 
            rounded-r-lg
            border-solid border-0 border-l-4 border-{borderColorClass} hover:bg-white cursor-pointer
            {classes}"
  on:click={() => onIconClick(name)}
>
  <Icon name={iconName} classNames={""} stroke={color} strokeWidth={2} />
  <p class="p-0 pl-{secondary ? '16 text-sm' : '4 text-lg'}">{title}</p>
</div>
